<template>
  <div class="pageContent">
    <el-card shadow="always">
      <el-form ref="form" :model="form" label-width="140px">
        <el-form-item label="手机号：" required>
          <el-input style="width:400px;" v-model.number="form.cellPhone" maxlength="11" show-word-limit></el-input>
          <div class="infoFont">员工可以使用该手机号座位账号登录筷赚后台</div>
        </el-form-item>
        <el-form-item label="姓名：" required>
          <el-input style="width:400px;" v-model.trim="form.userName" maxlength="10" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="密码：" required>
          <el-input style="width:400px;" v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="确认密码：" required>
          <el-input style="width:400px;" v-model="form.passwordAgain"></el-input>
        </el-form-item>
        <el-form-item label="邮箱：" required>
          <el-input style="width:400px;" v-model="form.email"></el-input>
        </el-form-item>
        <el-form-item label="状态：" required>
          <el-radio v-model="form.enabled" label="0">启用</el-radio>
          <el-radio v-model="form.enabled" label="1">禁用</el-radio>
        </el-form-item>
        <el-form-item label="角色：" required>
          <el-select v-model="form.roleId" clearable placeholder="请选择所属角色">
            <el-option v-for="(item,index) in roleList" :key="index" :label="item.roleName" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <div class="flex-row-center">
          <el-button @click="cancle">取消</el-button>
          <el-button type="primary" :loading="addLoading" @click="onSubmit">立即创建</el-button>
        </div>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { findRoleByShopId , findByCondition , add} from '@/api/systemSetting/userManage'
export default {
  data(){
    return{
      form:{
        enabled:'0'
      },
      addLoading:false,
      roleList:[],
    }
  },
  created(){
    this.getList()
  },
  methods:{
    cancle(){
      this.$confirm('确定放弃编辑?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$router.replace({path:'/systemSetting/userManage'})
      }).catch(() => {});
    },
    onSubmit(){
      if(!this.form.cellPhone){
        this.$message.error('请输入手机号');
        return;
      }
      if(!this.form.userName){
        this.$message.error('请输入姓名');
        return;
      }
      if(!this.form.password){
        this.$message.error('请输入密码');
        return;
      }
      if(this.form.password.length < 6 || this.form.password.length > 15){
        this.$message.error('密码应6~15位');
        return;
      }
      if(this.form.password != this.form.passwordAgain){
        this.$message.error('输入的两次密码不正确');
        return;
      }
      if(!this.form.email){
        this.$message.error('请输入邮箱');
        return;
      }
      if(!this.form.roleId){
        this.$message.error('请选择所属角色');
        return;
      }
      this.addLoading = true;
      this.form.shopId = this.shop.shopId
      add(this.form).then(res=>{
        console.log('添加用户',res)
        if(res.code == 200){
          this.$message({
            message:res.message,
            type: 'success'
          });
          setTimeout(()=>{
            this.$router.replace({
              path:'/systemSetting/userManage'
            })
            this.addLoading = false;
          },1000)
        }else{  
          this.$message.error(res.message);
          this.addLoading = false;
        }
      })
    },
    getList(){
      findRoleByShopId({shopId:this.shop.shopId}).then(res=>{
        console.log('所有角色',res)
        if(res.code == 200){
          this.roleList = res.data
        }
      })
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>

</style>